body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Geneva, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-color);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-color: #18191A;
  --bg: #242526;
  --bg-accent: #484a4d;
  --text-color: #dadce1;
  --button-color: #484a4d;
  --menu-hover-color: #525357;
  --nav-size: 60px;
  --border: 1px solid #474a4d8d;
  --border-radius: 8px;
  --speed: 150ms;
}

[data-theme="light"] {
  --bg-color: #F0F2F5;
  --bg: #FFFFFF;
  --bg-accent: #F7F8FA;
  --text-color: #000000;
  --button-color: #E4E6EB;
  --border: 1px solid #dde3eb8d;
  --menu-hover-color: #F2F2F2;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

.navbar {
  height: var(--nav-size);
  background-color: var(--bg);
  padding: 0 1rem;
  border-bottom: var(--border);
  box-shadow: rgba(0, 0, 0, 0.055) 0px 4px 12px;
}

.navbar-nav {
  max-width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.nav-item {
  width: calc(var(--nav-size) * 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button {
  --button-size: calc(var(--nav-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  background-color: var(--button-color);
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: filter 300ms;
  cursor: pointer;
}

.icon-button:active {
  transition-duration: var(--speed);
  transform: scale(0.96);
}

.icon-button:hover {
  filter: brightness(1.2);
}

.icon-button svg {
  fill: var(--text-color);
  width: 20px;
  height: 20px;
  transition-duration: var(--speed);
}

.icon-button-selected {
  --button-size: calc(var(--nav-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  background-color: #263850;
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: filter 300ms;
}

.icon-button-selected:active {
  transition-duration: var(--speed);
  transform: scale(0.96);
}

.icon-button-selected:hover {
  filter: brightness(1.2);
}

.icon-button-selected svg {
  fill: #2D86FF;
  width: 20px;
  height: 20px;
}

.dropdown {
  position: absolute;
  top:58px;
  width: 300px;
  transform: translateX(-45%);
  background-color: var(--bg);
  border: var(--border);
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  transition: height var(--speed) ease;
}

.menu-item {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  transition: background var(--speed);
  padding: 0.5rem;
  font-weight: bold;
}


.menu-item:hover {
  background-color: var(--menu-hover-color);
}

.menu-item-no-click {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  cursor: default;
  padding: 0.5rem;
  font-weight: bold;
}

.icon-right {
  margin-left: auto;

  fill: var(--text-color);
  width: 10px;
  height: 10px;
}
/*Sliders*/
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-right: 10px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

/*Classes for css transition group*/

.menu-primary-enter {
  position: absolute;
  transform: translateX(-210%);
}

.menu-primary-enter-active {
  transform: translateX(0%); 
  transition: all calc(var(--speed)*2) ease;
}

.menu-primary-exit {
  position: absolute;
  transform: translateX(0%);
}

.menu-primary-exit-active {
  transform: translateX(-210%);
  transition: all calc(var(--speed)*2) ease;
}

.menu-secondary-enter {
  position: absolute;
  transform: translateX(210%);
}

.menu-secondary-enter-active {
  transform: translateX(0%); 
  transition: all calc(var(--speed)*2) ease;
}

.menu-secondary-exit {
  position: absolute;
  transform: translateX(0%);
}

.menu-secondary-exit-active {
  transform: translateX(210%);
  transition: all calc(var(--speed)*2) ease;
}